export default {
	getMyDay(date) { ///
		let arr = date.split(" "),
			week = "",
			oday = "";
		if (arr[0]) { //
			let _date = new Date(arr[0])
			switch (_date.getDay()) {
				case 0:
					week = '';
					break;
				case 1:
					week = '';
					break;
				case 2:
					week = '';
					break;
				case 3:
					week = '';
					break;
				case 4:
					week = '';
					break;
				case 5:
					week = '';
					break;
				case 6:
					week = '';
					break;
			}
		}
		if (arr[1]) { //
			let _date = arr[1].split(':'),
				_oday = '';
			let _hours = _date[0];
			if (_hours < 6) {
				_oday = '';
			} else if (_hours < 7) {
				_oday = "";
			} else if (_hours < 12) {
				_oday = "";
			} else if (_hours < 13) {
				_oday = "";
			} else if (_hours < 17) {
				_oday = "";
			} else if (_hours < 19) {
				_oday = "";
			} else if (_hours < 24) {
				_oday = "";
			}
			if (_hours > 12) {
				_hours = Number(_hours) - 12;
			}
			oday = _oday + _hours + ':' + _date[1]
		}
		return week + ' ' + oday;
	},
	monthday(year, month) { //
		let date = new Date(year, month, 1);
		let _days = new Date(date.getTime() - 864e5).getDate();
		return _days;
	},
	// /0:0:0/23:59:59
	monthDay(year, month, val) {
		let _month = month < 10 ? '0' + month : month;
		if (val == 1) {
			let days = year + '/' + _month + '/01' + ' 0:0:0';
			return new Date(days).getTime()
		}
		if (val == 2) {
			let lastDay = new Date(year, month, 0).getDate();
			let days = year + '/' + _month + '/' + lastDay + ' 23:59:59';
			return new Date(days).getTime()
		}
	},
	// /0:0:0/23:59:59
	monthYear(year, val) {
		if (val == 1) {
			let days = year + '/' + '01/01' + ' 0:0:0';
			return new Date(days).getTime()
		}
		if (val == 2) {
			let lastDay = new Date(year, 12, 0).getDate();
			let days = year + '/12/' + lastDay + ' 23:59:59';
			return new Date(days).getTime()
		}
	},
	// yyyy-mm-dd hh:mm
	Format(e) {
		let _month = e.getMonth() + 1,
			_day = e.getDate();
		_month = _month < 10 ? '0' + _month : _month;
		_day = _day < 10 ? '0' + _day : _day;
		return `${e.getFullYear()}-${_month}-${_day} ${e.getHours()}:${e.getMinutes()}`;
	},
	// 
	compare(property) {
		return function(a, b) {
			var value1 = a[property];
			var value2 = b[property];
			return value1 - value2;
		}
	},
	// 
	randomColor() {
		return '#' + Math.floor(Math.random() * 0xffffff).toString(16);
	},
	//minNummaxNum
	randomNum(minNum, maxNum) {
		switch (arguments.length) {
			case 1:
				return parseInt(Math.random() * minNum + 1, 10);
				break;
			case 2:
				return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
				break;
			default:
				return 0;
				break;
		}
	},
	/**
	 * 
	 * @param int  value 
	 * @return string result 
	 */
	formatSeconds(value) {
		var theTime = parseInt(value); //  
		var theTime1 = 0; //  
		var theTime2 = 0; //  
		var theTime3 = 0; // 
		if (theTime > 60) {
			theTime1 = parseInt(theTime / 60);
			theTime = parseInt(theTime % 60);
			if (theTime1 > 60) {
				theTime2 = parseInt(theTime1 / 60);
				theTime1 = parseInt(theTime1 % 60);
				if (theTime2 > 24) {
					//24
					theTime3 = parseInt(theTime2 / 24);
					theTime2 = parseInt(theTime2 % 24);
				}
			}
		}
		var result = '';
		if (theTime > 0) {
			result = "" + parseInt(theTime) + "";
		}
		if (theTime1 > 0) {
			result = "" + parseInt(theTime1) + "" + result;
		}
		if (theTime2 > 0) {
			result = "" + parseInt(theTime2) + "" + result;
		}
		if (theTime3 > 0) {
			result = "" + parseInt(theTime3) + "" + result;
		}
		return result;
	},
	// JS 
	SplitArr(data, value) {
		let dataArr = [];
		data.map(mapItem => {
			console.log('mapItem:', mapItem)
			if (dataArr.length == 0) {
				dataArr.push({
					useMonth: mapItem.useMonth,
					useYear: mapItem.useYear,
					list: mapItem.list
				})
			} else {
				let res = dataArr.some(item => { //
					console.log("item:", item)
					if (item[value] == mapItem[value]) {
						item.list = item.list.concat(mapItem.list)
						return true
					} else {
						console.log('aa')
					}
				})
				if (!res) { //
					dataArr.push({
						useMonth: mapItem.useMonth,
						useYear: mapItem.useYear,
						list: mapItem.list
					})
				}
			}
		})
		return dataArr
	},
	// 
	checkPhone(phone) {
		if ((/^1[3456789]\d{9}$/.test(phone))) {
			return true;
		}else{
			return false;
		}
	}
}
